html {
    box-sizing: border-box;
}
 
body {
    margin: 0;
    padding: 0;
}
 
.common-bg{
    background-image: url( "../images/main/bg-image2.png" ), url( "../images/main/bg-image.png" );
    background-size:1920px 1371px, 1920px 815px; 
    background-position:0px 900px ,0 0;
    background-repeat:no-repeat;
}

/* wrapper inner */
.common-wrapper__header{
    margin: 0px; 
}
.common-wrapper__container{
    margin: 0px; 
}


/* grid 내그룹 111*/
.common-grid--col3__111 .box-cgc{
    padding: 8px; 
    /* background-color: rgb(90, 47, 189) ; */
}
/* grid 내그룹 21*/
.common-grid--col3__21 .box-cgc{
    padding: 8px; 
    /* background-color: rgb(90, 47, 189) ; */
}



/* flex column 아래방향 */
.common-flex--column{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}
/* 인기그룹 CardViewSmall 리스트 외곽뷰 */
.common-flex--column .item-basci{
    /* height: 93px; */
    border-radius: 13px;
    background: #FFFFFF;
    box-shadow: 0px -10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 13px;
    margin-bottom: 10px;
}
/* 인기그룹 / 추천치료사 타이틀 */
.common-flex--column .item-title-basci{
    height: 42px;
    /* background: #FFFFFF; */
}


/* flex row 오른쪽방향 */
.common-flex--row{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}


/* flex row 왼쪽방향 */
.common-flex--row__right{
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    align-items: center;
}
/* flex 양쪽 제목 left - right 내그룹 */
.common-flex--row-left-right{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-left: 8px;
    margin-right: 8px;
    /* height: 44px; */
    align-items: center;
}



/* chip */
.common-chip:hover {
    background: #573300;
    color: #FFFFFF;
}
/* 홈화면 상단 큰거 */
.common-chip{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 7px 17px 7px 17px;
    margin: 0px 10px 10px 0px ;
    background: #FFFFFF;
    border: 1px solid #573300;
    box-sizing: border-box;
    border-radius: 17px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #573300;
}
/* 로그인버튼 중간거 */
.common-chip--height__small{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2px 10px 2px 10px;
    margin: 0px 0px 0px 5px ;
    background: #FFFFFF;
    border: 1px solid #573300;
    box-sizing: border-box;
    border-radius: 17px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #573300;
}
/* 카드안에 제일 작은거 */
.common-chip--height__mini{
    padding: 2px 5px 2px 5px;
    margin: 0px 4px 0px 0px ;
    background: #FFFFFF;
    border: 1px solid #573300;
    box-sizing: border-box;
    border-radius: 17px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 10px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #573300;
    margin-right: 4px;
}


/* button add 신규그룹 */
.common-button__group--add{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 103px;
    height: 34px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    background: #573300;
    border-radius: 8px;
    letter-spacing: 0.03em;
    color: #FFFFFF;
}

