/*  body 상단 */

/* flex 굿로드 & 검색 & 검색버튼 */
.flex-top-bottom__center{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    flex-wrap: wrap;
}
/* flex 우리아이에게 딱 맞는 */
.flex-top-bottom__baseline{
    display: flex;
    justify-content: center;
    align-items: stretch;
    padding: 5px;
    flex-wrap: wrap;
}

/* search */
.search{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    height: 50px;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 25px;
}
.search-input{
    margin-left: 10px;
}
.search-button{
    margin-right: 10px;
}

/* 더보기 긴 버튼 */
.item-add-basci{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 21px;
    background: #573300;
    border-radius: 5px;
}
