.group-add-bg{       
    background-image: url( "../images/main/bg-group-add.png" );
    background-size:1796px 773px; 
    /* background-position:0px 0px; */
    background-repeat:no-repeat;
}

.group-add-header{ 
    background: #573300;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
}

.group-button-top{
    display: inline-block;
    width: 72px;
    height: 34px;
    background: #FF9100;
    border-radius: 8px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    line-height: 34px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #FFFFFF;
    margin-left: 10px;
}

.group-button-bottom{
    display: inline-block;
    width: 380px;
    height: 46px;
    background: #573300;
    border-radius: 5px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 19px;
    line-height: 46px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #FFFFFF;
    margin-left: 0px;
    margin-top: 10px;
}

.group-add-step1-menu{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;  
    align-items: center;
    justify-content: space-around;
    /* height: 46px; */
    margin-left: 20px;
    margin-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    background: #FFFFFF;
    border: 1px solid #FFBF26;
    box-sizing: border-box;
    border-radius: 30px;
    
    /* width: 400px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;  */
}

/* 스크롤 */
.common-row-scroll{
    margin: 4px, 4px;
    padding: 4px;
    width: 500px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
}

.group-title-box{
    background: #FFEFCB;
    border: 2px solid #573300;
    box-sizing: border-box;
    border-radius: 14px;
}

.group-content{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
} 
