/* 추천치료사 박스 */
.card-box__white{
    height: 300px;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 13px;
}
.card-box__content{    
    height: 0px;
    border: 1px solid rgba(87, 51, 0, 0.15);
    margin-left: 30px;
    margin-right: 30px;
}
.card-box__line{    
    height: 0px;
    border: 1px solid rgba(87, 51, 0, 0.15);
    margin-left: 30px;
    margin-right: 30px;
}



/* card 284 flex column 아래방향*/
.card-height--284{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 284px;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 13px;
}
.card-height--284 .card-blank{
    display: flex;
    flex-direction: column-reverse;
    height: 167px;
    /* background-image: url( "../images/main/group-image1.png" ); */
    background-repeat: no-repeat;
    background-size: cover;
} 
.card-title{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    background: rgba(255, 255, 255, 0.85);
    padding-left: 24px;
    height: 34px;
} 

.card-content{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    height: 23px;
} 

.card-teacher{
    display: inline-block;
    background-image: url( "../images/main/teacher-391.png" );
    background-repeat: no-repeat;
    background-size: cover;
    height: 22px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 10px;
    line-height: 22px;
    letter-spacing: 0.03em;
    color: #FFFFFF;
    padding-left: 16px;
    padding-right: 10px;
    margin-left: 10px;
}
